import Conductor from './conductor.js';
import SkewedSinusoidController from './controller/skewed-sinusoid-controller.js';
import RangeController from './controller/range-controller.js';
import LazyRangeController from './controller/lazy-range-controller.js';

let conductor = null;


function init() {


    let controllers = [];

    let sinusoidContinuousSlider;
    if (hasElement('sinusoid-continuous-slider')) {
        sinusoidContinuousSlider = new RangeController('sinusoid-continuous-slider');
        controllers.push(sinusoidContinuousSlider);
    }

    let sinusoidContinuousSliderRadius;
    if (hasElement('sinusoid-continuous-slider')) {
        sinusoidContinuousSliderRadius = new LazyRangeController('sinusoid-continuous-slider-radius');
        controllers.push(sinusoidContinuousSliderRadius);
    }

    if (hasElement('complex-sinusoid')) {
        let controller = new SkewedSinusoidController('complex-sinusoid');
        if (sinusoidContinuousSlider) {
            sinusoidContinuousSlider.onValueChange.push(val => controller.setAnglePosition(val));
        }
        if (sinusoidContinuousSliderRadius) {
            sinusoidContinuousSliderRadius.onValueChange.push(val => controller.setRadius(val));
        }
        controllers.push(controller);
    }


    conductor = new Conductor(controllers);
    conductor.start();
}

function hasElement(id) {
    return document.getElementById(id) != null;
}

/**
 * Configure the canvases to be able to handle screens with higher dpi.
 * 
 * We can only call this once because after that, the width has changed!
 */
function updateCanvasSizes() {
    const pixelRatio = window.devicePixelRatio || 1;
    const canvases = document.getElementsByTagName("canvas");
    for (let canvas of canvases) {
        const width = canvas.width;
        const height = canvas.height;
        canvas.width = width * pixelRatio;
        canvas.height = height * pixelRatio;
        canvas.style.width = width + 'px';
        canvas.style.height = height + 'px';
    }
}

// updateCanvasSizes();
init();


